<template>
<v-dialog max-width="600px">
        <v-btn ma-3 dark slot="activator">Sign in</v-btn>
    <v-card pa-3>
      <v-card-title>
        <h2>Sign in to your account</h2>
      </v-card-title>
      
    <v-card-text>
                <v-form>
                  <v-text-field
            mb-2
            prepend-icon="email" 
            v-model="email"
            :rules="emailRules"
            required type="text" 
            placeholder="Enter your email address"> 
            </v-text-field>
                 <v-text-field 
            mb-2
            prepend-icon="https"  
            type="password"
            v-model="password" 
            placeholder="Password"> 
            </v-text-field>

                </v-form><v-btn 
    primary 
    color="indigo lighten-1"
    large
    success  
    @click="signin">Sign in</v-btn>
              </v-card-text>
    
  </v-card>
  
      
    </v-dialog>
</template>

<script>
 import firebase from 'firebase';
  export default {
    name: 'Signin',
    data() {
      return {
        email: '',
        password: ''
      }
    },
    methods: {
      signin: function() {
        firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(
          () => {
            this.$router.replace('Booktaxi')
          },
          (err) => {
            alert('Oops. ' + err.message)
          }
        );
      }
    }
  }
</script>

<style scoped>  /* "scoped" attribute limit the CSS to this component only */
  input {
    margin: 10px 0;
    width: 20%;
    padding: 15px;
  }
 
  p a {
    text-decoration: underline;
    cursor: pointer;
  }
</style>