import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Contact from './views/Contact.vue'
import Payments from './views/Payments.vue'
import Booktaxi from './views/Booktaxi.vue'
import Bookings from './views/Bookings.vue'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      
    },
    {
      path: '/payments',
      name: 'payments',
      component: Payments,
     
    },
  
    {
      path: '/contact',
      name: 'contact',
      component: Contact,
     
    },
    {
      path: '/payments',
      name: 'payments',
      component: Payments,
      
    },
    {
      path: '/bookings',
      name: 'bookings',
      component: Bookings,
      
    },
    
    {
      path: '/booktaxi',
      name: 'booktaxi',
      component: Booktaxi,
     
    },
    
  ]
})

export default router;
