<template>
<v-dialog max-width="600px">
        <v-btn dark slot="activator">Sign up</v-btn>
    <v-card pa-3>
      <v-card-title>
        <h2>Create new account</h2>
      </v-card-title>
      
    <v-card-text>
                <v-form>
                  <v-text-field
            mb-2
            prepend-icon="email" 
            v-model="email"
            :rules="emailRules"
            required type="text" 
            placeholder="Enter your email address"> 
            </v-text-field>
                 <v-text-field 
            mb-2
            prepend-icon="https"  
            type="password"
            v-model="password" 
            placeholder="Password"> 
            </v-text-field>

                </v-form> <v-btn 
    primary 
    color="indigo lighten-1"
    large
    success  
    @click="signUp">Sign up</v-btn>
              </v-card-text>
   
  </v-card>
</v-dialog>
</template>

 <script>
  import firebase from 'firebase';
  export default {
    name: 'Signup',
    data() {
      return {
        email: '',
        password: ''
      }
    },
    methods: {
      signUp: function() {
        firebase.auth().createUserWithEmailAndPassword(this.email, this.password).then(
          () => {
            this.$router.replace('Booktaxi')
          },
          (err) => {
            alert('Oops. ' + err.message)
          }
        );
      }
    }
  }
</script>

 <style scoped>
  .sign-up {
    margin-top: 40px;
  }
  input {
    margin: 10px 0;
    width: 20%;
    padding: 15px;
  }

  span {
    display: block;
    margin-top: 20px;
    font-size: 11px;
  }
</style>