<template>
  <div>
     <v-layout row xs12 >
    <v-flex xs12 md12 ma-3>
     <v-card pa-3>
      <v-card-title>
        <h2>Book Taxi</h2>
      </v-card-title>
      <v-flex lg4 md4 sm4 ml-3>
      <v-form >
          <v-text-field placeholder="From"></v-text-field>
          <v-text-field placeholder="Where"></v-text-field>
           <v-date-picker color="indigo" v-model="picker"></v-date-picker><br>
          <v-btn>Submit</v-btn>
      </v-form>
      </v-flex>
    <v-card-text>
    <gmap-map
      :center="center"
      :zoom="12"
      style="height: 400px;"
    >
      <gmap-marker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        @click="center=m.position"
      ></gmap-marker>
    </gmap-map>
    
    </v-card-text>
    </v-card>
    </v-flex>
     </v-layout>
  </div>

</template>

<script>
export default {
  name: "GoogleMap",
  data() {
    return {
      picker: new Date().toISOString().substr(0, 10),
      center: { lat: 57.048820, lng: 9.921747 },
      markers: [],
      places: [],
      currentPlace: null
    };
  },

  mounted() {
    this.geolocate();
  },

  methods: {
    // receives a place object via the autocomplete component
    setPlace(place) {
      this.currentPlace = place;
    },
    addMarker() {
      if (this.currentPlace) {
        const marker = {
          lat: this.currentPlace.geometry.location.lat(),
          lng: this.currentPlace.geometry.location.lng()
        };
        this.markers.push({ position: marker });
        this.places.push(this.currentPlace);
        this.center = marker;
        this.currentPlace = null;
      }
    },
    geolocate: function() {
      navigator.geolocation.getCurrentPosition(position => {
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
      });
    }
  }
};
</script>